  .btn--stroke:hover {
    background-color: #b01c32;
    color: #fff;
  }
}
.btn--stroke:active {
  background-color: #b01c32;
  color: #fff;
}
.btn--white {
  border: 1px solid #fff;
  color: #fff;
  background-color: transparent;
}
