  max-width: unset;
}
.b-a__after {
  height: 100%;
}
.b-a__change {
  position: absolute;
  z-index: 4;
  cursor: pointer;
  left: 50%;
  top: 0;
  width: 0.3rem;
  height: 100%;
  background-color: #fff;
}
.b-a__change::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 4rem;
  height: 4rem;
  border-radius: 100%;
  background-image: url("./../img/before-after/arrows.svg");
  background-size: 1.5rem;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #fff;
}

