.quiz-container {
  margin-bottom: 10rem;
  &__title {
    margin-bottom: 4rem;
  }
  &__wrap {
    padding: 6rem;
    background: #f6f7f8;
    border-radius: 1.2rem;
  }
}

.quiz {
  &__row {
    display: flex;
    gap: 2rem;
    max-width: 75rem;

    &--long {
      max-width: 94rem;
    }
  }
  &__label {
    display: block;
    margin-bottom: 2rem;
    font-size: 2.4rem;
    color: #303030;
    font-weight: 700;
    span {
      font-size: 2rem;
      color: #8e8e8e;
    }
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem 0rem;
    max-width: 75rem;
    &--once {
      grid-template-columns: 1fr;
    }
  }

  &__quest {
    &:not(:last-child) {
      margin-bottom: 6rem;
    }
  }
  &__wrap {
    display: block;
    flex: 1;
  }
  &__input {
    display: block;
    width: 100%;
    height: 5.4rem;
    padding: 0 2rem;
    border: 1px solid #454545;
    border-radius: 1.2rem;

    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1.6rem;
  }

  &__bottom {
    display: flex;
    justify-content: center;
  }

  &__btn {
    width: 30rem;
  }
}

.quiz-check {
  display: flex;
  align-items: center;
  gap: 1rem;
  cursor: pointer;
  white-space: nowrap;
  input:checked ~ &__box {
    border-color: #b01c32;
    &::after {
      opacity: 1;
    }
  }
  input:checked ~ &__text {
    color: #b01c32;
  }
  &__box {
    width: 2rem;
    height: 2rem;
    border-radius: 4px;
    flex-shrink: 0;
    border: 2px solid #454545;
    position: relative;
    overflow: hidden;
    transition: 0.15s;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: #b01c32;
      background-image: url("./../img/check.svg");
      background-size: 1.2rem 0.9rem;
      background-position: center;
      background-repeat: no-repeat;
      opacity: 0;
      transition: 0.15s;
    }
  }
  &__text {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 2rem;
    color: #454545;
    transition: 0.15s;
  }
}
